import './AdminPedidos.css'

import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import Select from 'react-select'

import { Page, SDataTable } from '../../components'
import { ORDEN_VENTA_ESTADOS_SELECT } from '../../constants'
import { protectedRef } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const moment = require('moment-timezone')

export const AdminOrdenVenta: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const DataTable = React.createRef<any>()
  const [Estado, setEstado] = useState<any>(null)
  const [Fabricante, setFabricante] = useState<any>(null)
  const [Fabricantes, setFabricantes] = useState<any[]>([])
  const [MetodoEntrega, setMetodoEntrega] = useState<any>(null)

  const setup = () => {
    obtenerFabricantes()
  }

  React.useEffect(() => {
    setup()
  }, [])

  const obtenerFabricantes = () => {
    rootStore.environment.api.fabricantesList().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data.map((dat: any) => {
          return { value: dat.id, label: dat.nombre }
        })
        setFabricantes(data)
      }
    })
  }

  const visualizar = (row: any) => {
    history.push('/admin/orden_venta/detail/' + row.id)
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => visualizar(row)}>
          <FIcon.Eye size={14} />
        </span>
      </div>
    )
  }

  const estados_all_filter = function <T>(original: T): { label: string; value: number }[] {
    // @ts-ignore
    return [{ value: -1, label: 'Todos' }].concat(original)
  }

  useEffect(() => {
    DataTable && DataTable.current.refresh()
    DataTable && DataTable.current.clearSelected()
  }, [Estado, Fabricante, MetodoEntrega])

  useEffect(() => {
    if (history.location.hasOwnProperty('estado')) {
      // @ts-ignore
      const estado = history.location.estado
      if (estado === 'pendientes') {
        setEstado(10)
        setTimeout(() => {
          DataTable && DataTable.current.refresh()
        }, 1000)
      } else {
        setEstado(20)
        setTimeout(() => {
          DataTable && DataTable.current.refresh()
        }, 1000)
      }
    }
  }, [])

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Ordenes de Venta</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Ordenes de Venta</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <div className={'row'}>
                <div className={'col-4'}>
                  <h4 className={'mb-4'}>Estado</h4>
                  <Select
                    defaultValue={estados_all_filter([])[0]}
                    onChange={(val) => {
                      setEstado(val ? val.value : -1)
                    }}
                    placeholder={'Elegir estado'}
                    options={estados_all_filter(ORDEN_VENTA_ESTADOS_SELECT)}
                    theme={(theme) => ({
                      ...theme,
                      colors: {
                        ...theme.colors,
                        primary: '#303AB2',
                        neutral50: 'black',
                      },
                    })}
                  />
                </div>
                <div className={'col-4'}>
                  <h4 className={'mb-4'}>Proveedor</h4>
                  <Select
                    defaultValue={estados_all_filter([])[0]}
                    onChange={(val) => {
                      setFabricante(val ? val.value : -1)
                    }}
                    placeholder={'Elegir proveedor'}
                    options={estados_all_filter(Fabricantes)}
                    theme={(theme) => ({
                      ...theme,
                      colors: {
                        ...theme.colors,
                        primary: '#303AB2',
                        neutral50: 'black',
                      },
                    })}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={protectedRef(DataTable)}
                subHeader
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  return rootStore.environment.api.ordenesVenta(page, perPage, order, search, {
                    estado: Estado == -1 ? null : Estado,
                    fabricante: Fabricante == -1 ? null : Fabricante,
                  })
                }}
                columns={[
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: true,
                    cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY'),
                  },
                  {
                    name: 'Referencia',
                    selector: 'referencia',
                    sortable: true,
                  },
                  {
                    name: 'Fabricante',
                    sortable: true,
                    cell: (row: any) => row.fabricante,
                    omit: !loginStore.administrador,
                  },
                  {
                    name: 'Estado',
                    selector: 'estado',
                    sortable: true,
                    cell: (row: any) => {
                      let estado = ''
                      switch (row.estado) {
                        case 0:
                          estado = 'Creado'
                          break
                        case 10:
                          estado = 'Confirmado'
                          break
                        default:
                          estado = 'Finalizado'
                      }
                      return estado
                    },
                  },
                  {
                    name: 'Fecha de retiro',
                    selector: 'fecha_retiro',
                    sortable: true,
                    cell: (row: any) => (row.fecha_retiro ? moment(row.fecha_retiro).format('DD/MM/YYYY H:mm') : '-'),
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminOrdenVenta
